body {
  background: gray;
}

.wrapper {
  height: 100vh;
  width: 100vw;
}

.original-publisher, .original-subscriber {
  position: absolute;
  z-index: 1;
  width: 240px;
  height: 130px;
  right: 0;
}

.original-screen--active {
  height: 100%;
}

.original-subscriber {
  top: 150px;
}

.original-subscriber--active {
  height: 100%;
}

.share-button {
  position: fixed;
  top: 50px;
}